














































































import { Component, Vue } from 'vue-property-decorator';
import { Toast } from 'vant';

@Component({
  components: {},
})

export default class Header extends Vue {
  private activeLocale: string = this.$i18n.locale;
  private showMenu: boolean = false;
  private checked: boolean = false;
  private showLocale: boolean = false;
  private showRate: boolean = false;
  private showAbout: boolean = false;
  private rateValue: any = 0;
  private menuPopupStyle: any = {
    width: '50%',
    height: '100%'
  };
  private rateStyle: any = {
    height: '30%',
    width: '80%',
    borderRadius: '8px'
  };
  private aboutStyle: any = {
    height: '20%',
    width: '80%',
    borderRadius: '8px'
  };
  private menuItems: any = [
    { title: 'Download CV', icon: 'down' },
    // { title: 'Introduction', icon: 'bulb-o' },
    // { title: 'Profile', icon: 'user-o' },
    // { title: 'Skills', icon: 'medal-o' },
    // { title: 'Experience', icon: 'cluster-o' },
    // { title: 'Projects', icon: 'chart-trending-o' },
    // { title: 'Contact', icon: 'phone-circle-o' },
    { title: 'rate', icon: 'thumb-circle-o' },
    { title: 'about', icon: 'info-o' },
  ];
  private localeList: any = [
    { name: 'en' },
    { name: 'zh' },
  ];


  /*
   * @Author: keanu
   * @LastAuthor: null
   * @Param: null
   * @Return: html objects
   * @Date: 2021-04-21
   * @LastEditDate: null
   * @Description: get menu popup container
  */
  private getContainer() {
    return document.querySelector('.home');
  }

  /*
   * @Author: keanu
   * @LastAuthor: null
   * @Param: item: any
   * @Return: html objects
   * @Date: 2021-04-21
   * @LastEditDate: null
   * @Description: switch language
  */
  private switchLocale(locale: any) {
    this.showLocale = false;
    this.$i18n.locale = locale;
    this.activeLocale = locale;
  }

  private selectMenu(index: any) {
    this.showMenu = false;
    if (index === 0) {
      Toast.loading({
        message: 'Loading...',
        forbidClick: true,
      });
    } else if (index === 1) {
      this.showRate = true;
    } else {
      this.showAbout = true;
    }
  }

  private rateSubmit() {
    this.showRate = false;
    this.rateValue = 0;
      Toast.success('Success');
  }

  private rateCancel() {
    this.showRate = false;
    this.rateValue = 0;
  }

}
